@import "./basic.scss";

$update: #0188e4;

.docsContainer {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 10;
  overflow-y: scroll;

  .docsGroup {
    position: relative;
    width: 100%;
    overflow-y: scroll;
    overflow-x: clip;
  }
  &::-webkit-scrollbar {
    width: 0;
  }
}

.docsGroup {
  position: relative;
  video {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    z-index: 0;
  }
  .navbar {
    @include flex-row;

    height: 100px;
    background-color: black;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 20;

    .col-12 {
      @include flex-row;
      @include justify-content-space-between;

      .brand {
        @include flex-row;
        @include justify-content-space-between;

        gap: 20px;

        img {
          width: 50px;
          height: 50px;
        }
        span {
          color: $update;
          font-size: 30px;
          font-weight: bold;
          line-height: 100px;
        }
      }

      .btns {
        @include flex-row;
        @include justify-content-space-between;
        .github {
          a {
            color: white;
            font-size: 20px;

            svg {
              width: 20px;
              height: 20px;
            }
          }
        }
      }

      .smallBtn {
        display: none;
      }
    }
  }
  .docsNavbarBack {
    background-color: RGBA(27, 27, 29, 0.8) !important;
  }
  .welcome {
    position: relative;
    background-image: linear-gradient(
      180deg,
      RGBA(46, 45, 44, 0.9),
      RGBA(27, 27, 29, 0.9)
    );
    .container {
      position: relative;
      padding: 100px 0;
      z-index: 1;

      .title {
        text-align: center;
        h1 {
          font-size: 40px;
          font-weight: bolder;
          color: $update;
          margin: 30px 0;
        }
      }
      .items {
        @include flex-side;

        padding: 15px;

        .col-4 {
          padding: 0 15px;

          .item {
            border: 1px solid white;
            padding: 30px;
            height: 100%;
            .icon {
              margin-bottom: 15px;
              svg {
                width: 30px;
                height: 30px;
                path {
                  fill: white;
                }
              }
            }
            .content {
              h2 {
                color: white;
                font-weight: bolder;
                font-size: 24px;
                margin-bottom: 15px;
              }
              p {
                color: white;
                font-size: 18px;
              }
            }
          }
        }
      }
    }
  }
  .overview {
    padding: 100px 20px;
    background-color: RGB(27, 27, 29);
    position: relative;
    z-index: 5;

    .col-12 {
      @include flex-side;
      @include justify-content-space-between;

      .col-6 {
        padding: 0 15px;

        .header {
          color: white;
          font-size: 30px;
          font-weight: bolder;
          margin-bottom: 15px;
        }
        .body {
          position: relative;

          .gettingItem {
            position: relative;
            margin: 15px 0;
            border-radius: 20px;
            border: 1px solid white;
            padding: 15px;
            padding-right: 50px;

            h2 {
              color: $update;
              font-size: 24px;
              font-weight: bolder;
              margin: 10px 0;
            }

            p {
              color: $update;
              font-size: 18px;
              margin: 10px 0;
            }
            svg {
              position: absolute;
              right: 20px;
              top: 20px;
              width: 30px;
              height: 30px;
              color: $update;
            }
          }
          .developerItem {
            position: relative;
            padding: 15px;
            border-radius: 20px;
            border: 1px solid white;
            margin: 15px 0;
            padding-right: 50px;

            svg:first-child {
              width: 25px;
              height: 25px;
              color: $update;
              display: inline-block;
              margin-right: 15px;
            }

            span {
              font-size: 25px;
              font-weight: bolder;
              color: $update;
            }

            svg:last-child {
              position: absolute;
              top: 50%;
              right: 20px;
              width: 30px;
              height: 30px;
              color: $update;
              transform: translateY(-50%);
            }
          }
        }
      }
    }
  }
  .storeData {
    background-color: RGB(27, 27, 29);
    border-top: 1px solid white;
    padding-top: 100px;
    padding-left: 40px;
    padding-right: 40px;
    padding-bottom: 50px;

    .col-12 {
      @include flex-side;
      @include justify-content-space-between;

      .col-4 {
        width: 33.33333%;
        padding: 0 15px;

        .storeItem {
          @include flex-row;
          @include justify-content-space-between;

          border: 1px solid $update;
          border-radius: 20px;
          padding: 15px;
          svg {
            width: 35px;
            height: 35px;
            margin-right: 15px;
            path {
              color: $update;
            }
          }
          h3 {
            font-size: 20px;
            font-weight: bolder;
            color: $update;
          }
          p {
            font-size: 15px;
            color: $update;
          }
        }
      }
    }
  }

  .footer {
    background-color: RGB(48, 56, 70);
    padding: 40px 50px 100px 50px;
    .col-12 {
      @include justify-contnet-flex-start;
      .item {
        margin-right: 50px;

        h1 {
          color: $update;
          font-size: 24px;
          font-weight: bolder;
          margin-bottom: 25px;
        }

        p {
          margin: 5px 0;
          a {
            color: $update;
            font-size: 16px;
          }
          svg {
            display: inline-block;
            margin-left: 7px;
            path {
              color: $update;
            }
          }
        }
      }
    }
  }
}

@mixin shifColor {
  background: radial-gradient(
    ellipse farthest-corner at left top,
    #ffffff 0%,
    #ffffac 8%,
    #d1b464 25%,
    #5d4a1f 62.5%,
    #5d4a1f 100%
  ); /* Define your gradient */
  -webkit-background-clip: text; /* Safari */
  background-clip: text; /* Other browsers */
  color: transparent;
}

.brightMode .docsGroup {
  .navbar {
    .github a {
      color: black !important;
    }
    background-color: rgba(226, 217, 220, 0.93);
    box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
  }
  .overview {
    background-color: rgba(226, 217, 220, 0.93);
    .header {
      color: black;
    }
    .gettingItem {
      border-color: black !important;
    }
    .developerItem {
      border-color: black !important;
    }
  }
  .header {
    color: black !important;
  }
}

@media (max-width: 1440px) {
}
@media (max-width: 1260px) {
}

@media (max-width: 1024px) {
}

@media (max-width: 991px) {
  .gettingItem {
    h2 {
      font-size: 20px !important;
    }
    p {
      font-size: 14px !important;
    }
  }
  .developerItem {
    span {
      font-size: 20px !important;
    }
  }
}

@media (max-width: 767px) {
  .navbar {
    .btns {
      transform: scale(0) !important;
    }
    .smallBtn {
      display: block !important;
      cursor: pointer !important;
      svg {
        width: 20px !important;
        height: 20px !important;
        color: #d1b464 !important;
      }
    }
    .btns.showBtns {
      position: absolute !important;
      right: 35px !important;
      top: 100% !important;
      transform: scale(1) !important;
      background-color: rgba(0, 0, 0, 0.7) !important;
      z-index: 100 !important;
      flex-direction: column !important;
      padding: 20px !important;
      gap: 20px !important;
      transition: all 0.5s;
      .darkMode-btn {
        svg {
          margin-left: inherit !important;
        }
      }
    }
  }
  .welcome {
    .items {
      flex-wrap: wrap;

      .col-4 {
        width: 100%;
        margin: 15px 0;
      }
    }
  }
  .overview {
    .header {
      text-align: center;
      margin-bottom: 25px;
    }
    .col-12 {
      flex-wrap: wrap;
      .col-6 {
        width: 100%;
        margin: 30px 0;
      }
    }
  }
  .storeData {
    .col-12 {
      flex-wrap: wrap;

      .col-4 {
        width: 100% !important;
        margin: 15px 0;
        .storeItem {
          justify-content: flex-start !important;
          gap: 20px;
        }
      }
    }
  }
  .footer {
    .col-12 {
      flex-wrap: wrap;
      .item {
        margin: 20px 0;
      }
    }
  }
}

