@import "./basic.scss";

$goldenColor: RGB(224, 203, 139);
$mainColor: RGB(255, 213, 83);

@mixin backInitial {
  border-radius: 10px;
  margin-bottom: 16px;
  backdrop-filter: blur(33px);
  background-color: rgba(54, 56, 64, 0.4);
  padding: 40px;
}

/* CSS */
.button-78 {
  margin-left: auto;
  margin-right: auto;
  appearance: none;
  backface-visibility: hidden;
  background-color: RGB(255, 213, 83);
  border-radius: 10px;
  border-style: none;
  box-shadow: none;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  display: block;
  font-family: Inter, -apple-system, system-ui, "Segoe UI", Helvetica, Arial,
    sans-serif;
  font-size: 15px;
  font-weight: 500;
  height: 50px;
  letter-spacing: normal;
  line-height: 1.5;
  outline: none;
  overflow: hidden;
  padding: 14px 30px;
  position: relative;
  text-align: center;
  text-decoration: none;
  transform: translate3d(0, 0, 0);
  transition: all 0.3s;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
}

.button-78:hover {
  background-color: #1366d6;
  box-shadow: rgba(0, 0, 0, 0.05) 0 5px 30px, rgba(0, 0, 0, 0.05) 0 1px 4px;
  opacity: 1;
  transform: translateY(0);
  transition-duration: 0.35s;
}

.button-78:hover:after {
  opacity: 0.5;
}

.button-78:active {
  box-shadow: rgba(0, 0, 0, 0.1) 0 3px 6px 0, rgba(0, 0, 0, 0.1) 0 0 10px 0,
    rgba(0, 0, 0, 0.1) 0 1px 4px -1px;
  transform: translateY(2px);
  transition-duration: 0.35s;
}

.button-78:active:after {
  opacity: 1;
}

@media (min-width: 768px) {
  .button-78 {
    padding: 14px 22px;
    width: 176px;
  }
}

/* CSS */
.button-85 {
  padding: 0.6em 2em;
  border: none;
  outline: none;
  color: rgb(205, 184, 119);
  background: #000000;
  border: 2px solid rgb(205, 184, 119);
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.button-85:before {
  content: "";
  background: linear-gradient(
    45deg,
    RGB(255, 213, 83),
    RGB(255, 213, 83),
    RGB(255, 213, 83),
    RGB(255, 213, 83),
    RGB(255, 213, 83),
    RGB(255, 213, 83),
    RGB(255, 213, 83),
    RGB(255, 213, 83),
    RGB(255, 213, 83)
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  -webkit-filter: blur(5px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing-button-85 20s linear infinite;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

@keyframes glowing-button-85 {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

.button-85:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #222;
  left: 0;
  top: 0;
  border-radius: 10px;
}

@media (max-width: 600px) {
  .button-85 {
    font-size: 12px;
  }
}

body {
  background: linear-gradient(180deg, rgba(8, 15, 53, 0), rgba(0, 0, 10, 0.9)),
    linear-gradient(333deg, rgba(153, 207, 255, 0.2), rgba(180, 255, 217, 0.08)),
    radial-gradient(
      circle at 77% 89%,
      rgba(125, 163, 169, 0.8),
      rgba(125, 163, 169, 0) 50%
    ),
    radial-gradient(
      circle at 15% 95%,
      rgba(125, 163, 169, 0.8),
      rgba(125, 163, 169, 0) 43%
    ),
    radial-gradient(
      circle at 65% 23%,
      rgba(137, 151, 119, 0.4),
      rgba(137, 151, 119, 0) 70%
    ),
    radial-gradient(
      circle at 10% 0%,
      rgba(187, 211, 204, 0.33),
      rgba(187, 211, 204, 0) 35%
    ),
    radial-gradient(
      circle at 11% 100%,
      rgba(131, 165, 203, 0.3),
      rgba(131, 165, 203, 0) 30%
    );
  background-color: rgba(8, 15, 53, 1);
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.navbar {
  background: rgba(54, 56, 64, 0.33);
  height: 60px;
  padding: 0 50px;

  @include flex-row;

  .row {
    @include flex-row;
    @include justify-content-space-between;

    .navs {
      @include flex-row;

      gap: 30px;
      .nav a {
        font-size: 16px;
        color: white;
        padding-bottom: 5px;
      }

      .nav-active a {
        border-bottom: 1px solid white;
      }
    }
  }
}

.market {
  padding: 20px;

  .container {
    @include backInitial;

    .table {
      width: 100%;
      border-collapse: collapse;
      padding: 20px;
      overflow-x: auto;

      .thead,
      .tbody {
        width: 100%;
        min-width: 900px;
      }
      .tr {
        gap: 25px;
        margin: 10px 0;
      }
      .th {
        text-align: center;
        color: white;
        height: 80px;
        width: 20%;
        font-size: 24px;
        font-weight: bolder;

        i {
          color: white;
        }
      }
      .td {
        text-align: center;
        width: 20%;
        font-size: 16px;
        height: 60px;
        color: white;

        span {
          color: white;
          font-size: 14px;
        }
        i {
          color: white;
          line-height: 40px;
        }
      }
      .bond {
        @include flex-row;
        @include justify-content-center;

        svg {
          margin-right: 10px;
        }
        p {
          font-size: 16px;
          color: white;
          font-weight: bolder;
        }
        span {
          font-size: 12px;
          color: #8e9dba;
          text-align: left;
        }
      }
      .discount {
        line-height: 60px;
      }
      .progress {
        width: 100px;
        height: 10px;
        border-radius: 8px;
        background-color: #8e9dba;
        display: inline-block;
        margin-left: 10px;

        .content {
          background-color: white;
          height: 100%;
          width: 40%;
          border-radius: 8px;
        }
      }
      .tbv {
        .progress {
          @include flex-row;
          @include mx-auto;

          margin-top: 5px;
        }
      }
      .soldOut {
        .btn {
          @include mx-auto;

          color: #8e9dba;
          border-radius: 8px;
          border: 1px solid #8e9dba;
          width: 130px;
          height: 33px;
          line-height: 33px;
          font-weight: bold;
          text-align: center;
          display: block;
        }
      }
    }
  }
}

.ending {
  padding: 20px;

  .container {
    @include backInitial;
  }

  .read {
    @include col-12;
    @include flex-row;

    gap: 30px;
    padding: 30px;

    .item {
      padding: 20px;

      h4 {
        color: white;
        font-size: 16px;
        font-weight: bolder;
        margin-bottom: 15px;
      }

      p {
        font-size: 14px;
        color: white;
      }

      div {
        text-align: center;
        padding-top: 30px;

        i {
          color: white;
          line-height: 40px;
        }
        span {
          color: white;
          font-size: 16px;
        }

        p {
          font-size: 16px;
          color: #8e9dba;
        }
      }
    }
  }

  .btns {
    @include col-12;
    @include flex-row;
    @include justify-content-center;

    .btn {
      color: #f8cc82;
      border-radius: 8px;
      border: 1px solid #f8cc82;
      width: 180px;
      height: 35px;
      line-height: 35px;
      font-weight: bold;
      text-align: center;
      display: block;
      font-size: 14px;
    }
  }
}

.partner {
  @include backInitial;
  @include col-12;
  margin: 40px 20px;

  h1 {
    color: white;
    font-size: 30px;
    font-weight: bolder;
    margin-bottom: 15px;
  }

  form {
    @include flex-row;
    @include justify-content-space-between;

    select,
    .search {
      color: white;
      border-radius: 4px;
      padding-right: 20px;
      cursor: pointer;
      border: 0;
      min-width: 16px;
      background-color: rgba(54, 56, 64, 0.8);
      line-height: 35px;
      height: 35px;
    }
    select {
      width: 300px;
      padding-left: 20px;
    }
    .search {
      position: relative;
      padding-left: 20px;

      i {
        position: absolute;
        top: 50%;
        left: 10px;
        z-index: 4;
        color: #8e9dba;
        transform: translateY(-50%);
      }
      input {
        background-color: transparent;
        border: 0;
        width: 100%;
        height: 100%;
      }
      input:focus {
        border: 0;
        outline: none;
      }
      .showBtn {
        position: absolute;
        top: 50%;
        right: 10px;
        z-index: 4;
        color: white;
        border-radius: 20px;
        font-size: 12px;
        border: 1px solid white;
        padding: 3px 5px;
        line-height: 20px;
        transform: translateY(-50%);
      }
    }
  }
}

.botto-container {
  @include col-12;
  @include flex-side;
  @include flex-wrap;

  padding: 20px;

  .col-3 {
    padding: 10px;
    @include col-3;
  }

  .botto {
    @include col-12;

    padding: 20px;
    cursor: pointer;
    transition: all 0.5s;
    height: 100%;

    h1 {
      color: white;
      text-align: center;
      margin-bottom: 15px;
      font-size: 18px;
      font-weight: bolder;
    }
    .image {
      text-align: center;

      svg {
        width: 60px;
        height: 60px;
        color: white;
      }
    }
    .letter {
      h4 {
        color: white;
        text-align: center;
        font-size: 18px;
        font-weight: bolder;
        margin-bottom: 10px;
        margin-top: 20px;
      }
      p {
        font-size: 15px;
        color: white;
        text-align: center;
        font-weight: normal;
      }
    }
  }
  .botto:hover {
    background-color: rgba(184, 186, 194, 0.5);
  }
}

@media (max-width: 991px) {
  .botto-container {
