  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 1000;
}
.modal-relative {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1002;
}
.modal-background {
  z-index: 1001;
  background: rgba(100, 100, 100, 0.1);
  backdrop-filter: blur(33px);
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
