.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
}
.darkMode {
  background-color: rgb(20, 23, 34);
}
.brightMode {
  background-color: RGB(225, 206, 127);
}
.app-sidebar {
  height: 100vh;
  width: 260px;
  overflow-y: scroll;
  overflow-x: hidden;
  transition: all 0.3s;
}
.app-content {
  width: calc(100% - 260px);
  height: 100vh;
  overflow-y: scroll;
}
::-webkit-scrollbar {
  display: none;
}
.darkMode .app-content {
  background-color: rgb(44, 46, 55);
}
.app-sidebar svg path {
  fill: #0188e4;
  transition: all 0.4s;
}
.sidebar-logo {
  padding: 40px;
