  cursor: pointer;
  margin: 20px 0;
}
.sidebar-logo p {
  color: #0188e4;
  font-size: 24px;
  transition: all 0.4s;
}
.sidebar-logo:hover svg path {
  fill: #f8cc82;
}
.sidebar-logo:hover p {
  color: #f8cc82;
}
.side-nav-item:hover path {
  fill: #f8cc82;
}
.side-nav-item path {
