@import "./basic1.scss";

* {
  letter-spacing: -0.03em;
  font-family: inter;
}
section {
  position: relative;
}

.launchBtn {
  background-image: linear-gradient(90deg, #1a61ed, #11bae3);
  color: white !important;
  display: inline-block;
  text-align: center;
}
.navbar {
  background-color: transparent !important;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  right: 0;
  height: 80px;
  z-index: 10;
  .container {
    position: relative;
    height: 100%;
    width: 100%;

    .col-12 {
      @include flex-row;
      @include justify-content-space-between;

      .header {
        .logo {
          padding: 0 15px;
          h1 {
            text-shadow: 1px 0 grey, 0 1px grey, -1px 0 grey, 0 -1px grey;
            color: #1587e7 !important;
            font-size: 30px;
            font-weight: bolder;
            display: block;
            line-height: 80px;
          }
        }
      }

      .nav-items {
        @include flex-row;

        .nav-item {
          margin: 0 15px;
          padding: 0 15px;
          line-height: 80px;
          color: white !important;
          font-size: 20px;
        }
      }
      .small-nav-items {
        display: none;
      }

      .social-links {
        @include flex-side;

        .social-link {
          @include flex-row;

          padding: 0 15px;
          border-radius: 8px;
          text-align: center;
          line-height: 50px;
          background-color: #0c2530 !important;
          margin: 0 15px;
          svg {
            color: white !important;
            width: 20px;
            height: 20px;
            display: inline-block;
          }
        }
        .btn-group {
          padding: 0 15px;
          .launchBtn {
            height: 50px;
            line-height: 50px;
            font-size: 20px;
            width: 200px;
            border-radius: 10px;
          }
        }
      }
      .small-social-links {
        display: none;
      }
    }
  }
}
.navbarBack {
  background-color: rgba($color: #0c2530, $alpha: 0.8) !important;
}
.overlay {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 0;
  background-image: linear-gradient(90deg, #0a3641, #0c0b15);
  opacity: 0.96;
}
@media (max-width: 565px) {
  .small-nav-items,
  .small-social-links {
    display: block !important;
    background-color: #0b2c42 !important;
    border-radius: 8px;
    padding: 10px;
    svg {
      color: white !important;
      width: 30px;
      height: 30px;
    }
  }
  .nav-items,
  .social-links {
    width: 0 !important;
    overflow: hidden;
  }
}
.container {
  z-index: 1;
  position: relative;
}

.section1 {
  @include back-img-set;

  padding-top: 90px;
  background-image: url("../imgs/landingFirst.png");

  .col-12 {
    @include flex-row;
    @include justify-content-center;
    padding: 30px 0;

    .content {
      margin: 30px 0;
      width: 50%;
      h1 {
        color: white !important;
        font-size: 40px;
        line-height: 50px;
        text-align: center;
        font-weight: bolder;
        margin-bottom: 15px;
      }
      p {
        color: white !important;
        text-align: center;
        margin-bottom: 30px;
        font-size: 20px;
      }
      div {
        @include flex-row;
        @include justify-content-center;
        margin-bottom: 30px;

        .launchBtn {
          width: 240px;
          height: 70px;
          font-size: 27px;
          line-height: 70px;
          border-radius: 10px;
          font-weight: 600;
        }
      }
    }
  }
}
@media (max-width: 565px) {
  .section1 {
    .col-12 {
      .content {
        width: 80%;
        h1 {
          font-size: 33px;
          line-height: 45px;
        }
      }
    }
  }
}

.section2 {
  @include back-img-set;
  @include flex-row;
  @include justify-content-center;

  padding: 50px 0;
  background-image: url("../imgs/landingSecond.png");

  .container {
    padding: 0 100px;
    .col-12 {
      @include flex-row;
      @include justify-content-space-around;
      padding: 50px;
      border-radius: 10px;

      transform: skew(-20deg);
      background-image: linear-gradient(90deg, #0d1732, #0c2530);

      .col-4 {
        padding: 0 20px;
        @include flex-row;
        @include justify-content-center;

        .item {
          p {
            font-size: 16px;
            color: white !important;
            transform: skew(20deg);
          }
          h1 {
            color: white !important;
            font-size: 30px;
            font-weight: bolder;
            transform: skew(20deg);
          }
        }
      }
    }
  }
}
@media (max-width: 565px) {
  .section2 {
    .container {
      padding: 0 !important;
    }
    .col-12 {
      flex-wrap: wrap;
      background-image: none !important;
      transform: skew(0) !important;

      .col-4 {
        transform: skew(-20deg);
        margin: 20px 0;
        padding: 30px 100px !important;
        border-radius: 8px;
        width: 100%;
        background-image: linear-gradient(90deg, #0d1732, #0c2530) !important;
      }
    }
  }
}
.section3 {
  padding: 100px 0;

  .col-12 {
    @include flex-row;

    .col-6 {
      padding: 0 15px;

      .text {
        background-image: url("../imgs/radial.png");
        background-size: contain;
        background-repeat: no-repeat;
        background-position: 0%;

        .content {
          padding: 20px 0;

          h1 {
            font-size: 50px;
            position: relative;
            display: inline-block;
            color: white !important;
            font-weight: bolder;

            span {
              position: absolute;
              left: 100%;
              bottom: 100%;
              color: white !important;
              font-size: 16px;
            }
          }

          h2 {
            margin: 15px 0;
            font-size: 45px;
            font-weight: bolder;
            color: white !important;
          }
          p {
            font-size: 16px;
            color: white !important;
          }
        }
      }

      .currency {
        width: 100%;

        .cryptos {
          width: 100%;
          @include flex-row;

          flex-wrap: wrap;

          .crypto {
            width: 50%;
            padding: 20px;
            border: 1px solid #216a81;
            .type {
              @include flex-row;

              div {
                margin-right: 15px;
                img {
                  width: 40px;
                  height: 40px;
                }
              }
              h2 {
                font-size: 24px !important;
              }
            }
            h1 {
              color: white !important;
              font-size: 32px;
              font-weight: bolder;
              padding: 0 20px;
            }
          }
        }
      }
    }
  }
}
@media (max-width: 565px) {
  .section3 {
    .col-12 {
      flex-wrap: wrap;

      .col-6 {
        width: 100%;
        margin: 30px 0;

        h1 {
          margin-left: auto;
          margin-right: auto;
        }
        h2 {
          text-align: center;
        }
        p {
          text-align: center;
        }
      }
    }
  }
}
.section4 {
  padding: 120px 0;
  .container {
    .col-12 {
      .title {
        margin-left: auto;
        margin-right: auto;
        width: 50%;
        margin-bottom: 30px;
        h1 {
          color: white !important;
          font-size: 50px;
          text-align: center;
          margin-bottom: 10px;
        }
        p {
          font-size: 24px;
          color: white !important;
          text-align: center;
        }
      }
      .content {
        .carousel {
          // height: 500px;
          padding: 50px 0;
          .carousel-cell {
            width: 33.33333%;
            margin-right: 15px;
            .item {
              padding: 0 15px;
              .img {
                text-align: center;
                background-color: #0b171f !important;
                border-radius: 50%;
                margin-left: auto;
                margin-right: auto;
                width: 120px;
                height: 120px;
                img {
                  width: auto;
                  height: 70px;
                  vertical-align: middle;
                  margin-top: 25px;
                  display: inline-block;
                }
              }
              h3 {
                color: white !important;
                font-size: 40px;
                margin-top: 30px;
                text-align: center;
              }
            }
          }

          .flickity-button {
            color: #0c3a4b;
            background-color: transparent !important;
          }
          .flickity-button-icon {
            width: 2rem;
            height: 2rem;
          }
        }
      }
    }
  }
}
@media (max-width: 565px) {
  .section4 {
    .title {
      width: 90% !important;
      h1 {
        font-size: 35px !important;
      }
    }
    .carousel-cell {
      width: 42.68% !important;
      h3 {
        font-size: 13px !important;
      }
    }
  }
}
.section5 {
  padding: 120px 0 30px;

  .title {
    h1 {
      font-size: 50px;
      color: white !important;
      text-align: center;
      font-weight: 900;
      margin-bottom: 35px;
    }
    .items {
      @include flex-row;
      @include justify-content-center;
      margin: 10px 0 20px;

      .item {
        margin: 0 20px;
        span {
          color: white !important;
          font-size: 26px;
          padding-bottom: 15px;
        }
      }
      .item.item-active {
        span {
          color: #1587e7 !important;
          border-bottom: 2px solid #1587e7;
        }
      }
    }
  }

  .content {
    .workCarousel {
      // height: 500px;
      padding: 50px 0;
      .workCarousel-cell {
        width: 33.33333%;
        margin-right: 15px;
        .item {
          padding: 0 50px;
          .img {
            text-align: center;
            background-color: #0b171f !important;
            border-radius: 50%;
            margin-left: auto;
            margin-right: auto;
            width: 120px;
            height: 120px;
            img {
              width: auto;
              height: 70px;
              vertical-align: middle;
              margin-top: 25px;
              display: inline-block;
            }
          }
          h3 {
            color: white !important;
            font-size: 24px;
            margin-top: 30px;
            text-align: center;
          }
          p {
            color: white !important;
            font-size: 16px;
            margin-top: 10px;
            text-align: center;
          }
        }
      }

      .flickity-button {
        color: #0c3a4b;
        background-color: transparent !important;
      }
      .flickity-button-icon {
        width: 2rem;
        height: 2rem;
      }
      .flickity-prev-next-button.next {
        right: -50px;
      }
      .flickity-prev-next-button.previous {
        left: -50px;
      }
    }
  }
}
@media (max-width: 565px) {
  .section5 {
    .title {
      .items {
        flex-wrap: wrap;

        .item {
          text-align: center;
          width: 100%;
          margin: 30px 0 !important;
        }
      }
    }
    .workCarousel-cell {
      width: 114% !important;
    }
  }
}
.section6 {
  padding: 120px 0;
  .col-12 {
    .title {
      margin-left: auto;
      margin-right: auto;
      width: 50%;
      margin-bottom: 30px;
      h1 {
        color: white !important;
        font-size: 50px;
        text-align: center;
        margin-bottom: 10px;
        font-weight: 900;
      }
      p {
        font-size: 24px;
        color: white !important;
        text-align: center;
      }
    }

    .content {
      @include flex-side;

      .items {
        width: 25%;

        .item {
          line-height: 33.33333%;
          height: 33.33333%;
          padding-left: 20px;

          p {
            @include flex-row;
            font-size: 24px;
            height: 100%;
            padding-left: 20px;
            color: white !important;
          }
        }
        .item-active {
          background-image: linear-gradient(90deg, #111937, #11bae3) !important;
        }
      }

      .text {
        @include flex-row;
        @include justify-content-center;
        width: 45%;
        background-image: linear-gradient(90deg, #111937, #102235) !important;
        padding-top: 20px;
        padding-left: 20px;
        padding-bottom: 20px;
        p {
          color: white !important;
          font-size: 17px;
          width: 50%;
        }
      }
      .img {
        width: 30%;
        background-image: url("../imgs/robotPlane.png");
        @include back-img-set;

        img {
          display: none;
        }
      }
    }
  }
}
@media (max-width: 565px) {
  .section6 {
    .title {
      width: 80% !important;
      h1 {
        font-size: 40px !important;
      }
    }
    .content {
      flex-wrap: wrap;

      .items,
      .text,
      .img {
        border-radius: 16px;
        margin: 20px 0;
        width: 100% !important;
      }
      .img {
        border-radius: 16px;
        img {
          display: block !important;
          width: 100% !important;
          border-radius: 16px;
          height: auto;
        }
      }
      .item {
        border-radius: 16px;
        p {
          text-align: center;
          line-height: 70px;
        }
      }
      .text {
        p {
          width: 75%;
          text-align: center;
        }
      }
    }
  }
}
.section7 {
  padding: 120px 0;

  .col-12 {
    .title {
      margin-left: auto;
      margin-right: auto;
      width: 50%;
      margin-bottom: 30px;
      h1 {
        color: white !important;
        font-size: 50px;
        text-align: center;
        margin-bottom: 10px;
      }
      p {
        font-size: 24px;
        color: white !important;
        text-align: center;
      }
    }
    .content {
      .dropdown-content {
        .dropdown-group {
          padding: 15px;
          border: 1px solid #216a81;
          // border-left: 1px solid #216A81;
          .dropdown-head {
            .icon {
              @include flex-row;
              @include justify-content-space-between;

              .texts {
                @include flex-row;
                p {
                  color: white !important;
                  background-color: #0b171f !important;
                  border-radius: 50%;
                  width: 70px;
                  height: 70px;
                  display: inline-block;
                  text-align: center;
                  font-size: 30px;
                  line-height: 70px;
                }
                h1 {
                  color: white !important;
                  font-size: 26px;
                  font-weight: bolder;
                  margin-left: 30px;
                }
              }
              .arrow {
                svg {
                  color: white !important;
                  width: 30px;
                  height: 30px;
                }
                svg:last-child {
                  display: none;
                }
              }
            }
          }
          .dropdown-body {
            overflow-y: hidden;
            height: 0;
            transition: all 0.5s ease;
            p {
              color: white !important;
              font-size: 19px;
              line-height: 30px;
              padding: 15px 100px;
            }
          }
        }

        .dropdown-groupShow {
          border-top: 1px solid #216a81;

          .dropdown-head {
            svg:first-child {
              display: none;
            }
            svg:last-child {
              display: block !important;
            }
          }

          .dropdown-body {
            height: 100px;
            max-height: 200px;
          }
        }
      }
    }
  }
}

@media (max-width: 565px) {
  .section7 {
    .title {
      width: 90% !important;

      h1 {
        font-size: 40px !important;
      }
    }
    .col-12
      .content
      .dropdown-content
      .dropdown-group
      .dropdown-head
      .icon
      .texts
      p {
      font-size: 30px !important;
      line-height: 50px !important;
      width: 50px !important;
      height: 50px !important;
    }
    .col-12
      .content
      .dropdown-content
      .dropdown-group
      .dropdown-head
      .icon
      .texts
      h1 {
      font-size: 20px;
    }
    .col-12
      .content
      .dropdown-content
      .dropdown-group
      .dropdown-head
      .icon
      .arrow
      svg {
      width: 20px;
      height: 20px;
    }
    .col-12 .content .dropdown-content .dropdown-groupShow .dropdown-body {
      height: 420px !important;
      max-height: 420px !important;
      p {
        padding: 14px 32px;
      }
    }
  }
}

.footer {
  padding: 120px 0;
  .topBar {
    width: 80%;
    height: 4px;
    background-color: #1587e7 !important;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 30px;
    position: relative;

    .svgContainer {
      @include flex-row;
      @include justify-content-center;

      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border-radius: 50%;
      background-color: #0c2530 !important;
      width: 60px;
      height: 60px;

      svg {
        width: 30px;
        height: 30px;
        color: white !important;
      }
    }
  }
  .content {
    display: flex;
    flex-direction: row;
    padding-top: 30px;

    .socials {
      margin-right: 80px;
      h1 {
        color: #1587e7 !important;
        font-size: 40px;
        font-weight: 900;
        margin-bottom: 30px;
      }
      p {
        color: white !important;
        font-size: 18px;
        margin-bottom: 30px;
        max-width: 280px;
      }
      .links {
        @include flex-row;

        .link {
          border-radius: 8px;
          margin-right: 10px;
          background-color: #0c3a4b !important;
          padding: 10px;
          svg {
            color: white !important;
          }
        }
        .link-active {
          background-color: #1587e7 !important;
        }
      }
    }
    .items {
      margin-right: 50px;
      h5 {
        color: white !important;
        font-size: 25px;
        margin-bottom: 20px;
      }
      .item {
        color: white !important;
        font-size: 16px;
        margin-bottom: 10px;
        max-width: 280px;
      }

      .footer-btn {
        position: relative;
        width: 100%;
        border-radius: 16px;
        margin-top: 40px;

        input {
          width: 100%;
          height: 60px;
          display: inline-block;
          line-height: 60px;
          padding: 0 15px;
          color: white !important;
          background-image: linear-gradient(90deg, #0b2042, #0b2c42);
          padding-right: 60px;
          border-radius: 8px;
          font-size: 18px;
          border-radius: 16px;
        }
        input::placeholder {
          color: white !important;
        }

        .svg {
          position: absolute;
          right: 10px;
          top: 10px;
          bottom: 10px;
          width: 40px;
          height: 40px;
          border-radius: 8px;
          background-image: linear-gradient(90deg, #196bec, #11b6e1);
          text-align: center;

          svg {
            color: white !important;
            width: 20px;
            height: 20px;
            margin-top: 10px;
          }
        }
      }
    }
  }
}

@media (max-width: 565px) {
  .footer {
    .links {
      .link {
        svg {
          width: 20px !important;
          height: 20px !important;
        }
      }
    }

    .socials,
    .items {
      margin-top: 25px !important;
      margin-bottom: 25px !important;
    }
  }
}

.nav-items-fixed {
  display: flex !important;
  background-color: #0b2c42 !important;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 320px !important;
  transition: all 0.5s ease;
  z-index: 20;
  flex-direction: column !important;
  justify-content: center;

  .nav-item {
    margin: 40px 0;
    font-size: 30px;
  }
  .nav-close-item {
    font-size: 40px !important;
    font-weight: bolder !important;
    color: white !important;
    margin-left: auto !important;
    cursor: pointer;
  }
}

.social-links-fixed {
  display: flex !important;
  background-color: #0b2c42 !important;
  position: fixed;
  right: 0;
  top: 0;
  bottom: 0;
  width: 320px !important;
  transition: all 0.5s ease;
  z-index: 20;
  flex-direction: column !important;
  justify-content: center;

  .social-link {
    flex-direction: row;
    align-items: center;
    padding: 0 15px;
    border-radius: 8px;
    text-align: center;
    line-height: 50px;
    background-color: #0c2530 !important;
    margin: 0 15px;
    width: 50px;
    height: 50px;
    margin-left: auto !important;
    margin-right: auto !important;
    margin-top: 30px !important;
  }
  .btn-group {
    text-align: center;
    padding: 30px 0 !important;
  }
  .social-close-link {
    background-color: transparent !important;
    background: transparent !important;
    font-size: 50px !important;
    margin-right: 20px !important;
    color: white !important;
    cursor: pointer;
    margin-left: auto !important;
  }
}
